import React, { useState, useLayoutEffect, useRef } from 'react';

type Props = {
    text: string
    speed?: number
    className?: string
    onComplete?: () => void
}

export default ({ text, speed = 100, className = '', onComplete = null }: Props) => {
    const len = text.length;
    const [cursor, setCursor] = useState(0);

    useLayoutEffect(() => {
        if (cursor < len) {
            const interval =
                window.setInterval(() => {
                    setCursor(cursor + 1);
                }, speed);

            return () => window.clearInterval(interval);
        } else {
            if (onComplete) onComplete();
        }
    });

    return (
        <span className={className}>
            {text.slice(0, cursor)
                .split('')
                .map((ltr, key) => <span key={key}>{ltr}</span>)
            }
            <i className="fa fa-i-cursor" style={{
                fontWeight: "normal",
                color: "rgba(0, 0, 0, 0.3)"
            }} />
        </span>
    )
}